/**********************
* Base style
**********************/
html,
body {
  height: 100%;
}

body {
  background: #fff;
  font-family: 'PT sans', sans-serif;
  font-size: 10px;
  color: #000;
  font-weight: 400;
  min-width: 335px;
}

img {
  max-width: 100%;
  height: auto;
}

section {
  position: relative;
}

ul {
  list-style: none;
  padding: 0;
}

br {
  font-size: 0;
}

* {
  box-sizing: border-box;
}

/*********************** typography **********************/
@font-face {
  font-family: "SFProDisplay-Bold";
  src: url("../../fonts/SFProDisplayBold/SFProDisplay-Bold.eot");
  src: url("../../fonts/SFProDisplayBold/SFProDisplay-Bold.woff2") format("woff2"), url("../../fonts/SFProDisplayBold/SFProDisplay-Bold.woff") format("woff"), url("../../fonts/SFProDisplayBold/SFProDisplay-Bold.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "SFProText-Light";
  src: url("../../fonts/SFProtextLight/SF Pro Text Light.eot");
  src: url("../../fonts/SFProtextLight/SF Pro Text Light.woff2") format("woff2"), url("../../fonts/SFProtextLight/SF Pro Text Light.woff") format("woff"), url("../../fonts/SFProtextLight/SF Pro Text Light.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "SFProText-Bold";
  src: url("../../fonts/SFProtextBold/SF Pro Text Bold.eot");
  src: url("../../fonts/SFProtextBold/SF Pro Text Bold.woff2") format("woff2"), url("../../fonts/SFProtextBold/SF Pro Text Bold.woff") format("woff"), url("../../fonts/SFProtextBold/SF Pro Text Bold.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "SFProText-Regular";
  src: url("../../fonts/SFProtextRegular/SF Pro Text Regular.eot");
  src: url("../../fonts/SFProtextRegular/SF Pro Text Regular.woff2") format("woff2"), url("../../fonts/SFProtextRegular/SF Pro Text Regular.woff") format("woff"), url("../../fonts/SFProtextRegular/SF Pro Text Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
hr {
  margin: 0;
  font-weight: 400;
}

a {
  display: inline-block;
}

.main {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 136px;
  padding-top: 172px;
}

.close {
  position: absolute;
  right: 16px;
  top: 14px;
}

.unlock {
  font-family: 'SFProText-Regular';
  font-size: 34px;
  line-height: 45px;
  text-align: center;
  color: #333;
  margin-bottom: 25px;
}

.plans {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  border: 1px solid #EE334B;
  height: 70px;
  border-radius: 35px;
  padding: 20px 15px 20px 30px;
  margin-bottom: 15px;
}

.plans__price {
  display: flex;
  flex-direction: column;
}

.plans__price_sale {
  font-family: 'SFProText-Regular';
  font-size: 22px;
  color: #ee334b;
  line-height: 17px;
  margin-bottom: 3px;
}

.plans__price_money {
  font-family: 'SFProText-Light';
  font-size: 14px;
  color: #ee334b;
  line-height: 17px;
}

.plans__action {
  width: 162px;
  font-family: 'SFProText-Light';
  line-height: 41px;
  font-size: 14px;
  text-align: center;
  background: linear-gradient(to right, #F5224B, #FF598F);
  border-radius: 21px;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
}

.options {
  margin: 0;
  margin-top: 20px;
}

.options__item {
  font-family: "SFProText-Light";
  font-size: 17px;
  line-height: 33px;
  padding-left: 25px;
  position: relative;
}

.options__item::before {
  content: url("data:image/svg+xml,%3Csvg width='11px' height='11px' viewBox='0 0 11 11' version='1.1' xmlns:xlink='http://www.w3.org/1999/xlink' xmlns='http://www.w3.org/2000/svg'%3E%3Cdesc%3ECreated with Lunacy%3C/desc%3E%3Cg id='days-checked-copy-7'%3E%3Cpath d='M5.499 0L5.501 0C8.53801 0 11 2.46199 11 5.499L11 5.501C11 8.53801 8.53801 11 5.501 11L5.499 11C2.46199 11 0 8.53801 0 5.501L0 5.499C0 2.46199 2.46199 0 5.499 0L5.501 0C8.53801 0 11 2.46199 11 5.499L11 5.501C11 8.53801 8.53801 11 5.501 11L5.499 11C2.46199 11 0 8.53801 0 5.501L0 5.499C0 2.46199 2.46199 0 5.499 0Z' id='Rectangle-5' fill='%23EE334B' stroke='none' /%3E%3Cpath d='M6.36319 0.434295L5.94912 0L2.22666 3.90429L0.413613 2.00269L0 2.4365L1.81304 4.33811L1.81299 4.33817L2.22706 4.77246L2.22711 4.7724L2.22715 4.77244L2.64076 4.33862L2.64073 4.33858L6.36319 0.434295Z' transform='translate(2.38623 3.181641)' id='Combined-Shape' fill='%23FFFFFF' fill-rule='evenodd' stroke='none' /%3E%3C/g%3E%3C/svg%3E");
  position: absolute;
  left: 0;
  top: 0;
  width: 12px;
  height: 12px;
}

.conditions {
  width: 100%;
  max-width: 319px;
  padding-left: 5px;
  padding-right: 5px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  text-align: center;
  margin-bottom: 30px;
}

.conditions__text {
  text-align: center;
  font-family: "SFProText-Regular";
  font-size: 11px;
  line-height: 15px;
  color: #000;
  opacity: 0.5568;
}

.conditions__link {
  text-decoration: none;
  font-family: "SFProText-Regular";
  font-size: 11px;
  line-height: 15px;
  color: #000;
  opacity: 0.5568;
}

.conditions__link:first-of-type {
  margin-right: 10px;
}

.opacity {
  position: fixed;
  bottom: 0;
  height: 42px;
  width: 100%;
  background: linear-gradient(to top, #fff, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0));
}

/**********************
* MEDIA
**********************/
/* Iphone X */
@media screen and (max-width: 380px) and (max-height: 820px) {
  .main {
    padding-top: 210px;
    margin-bottom: 148px;
  }
  .close {
    top: 57px;
  }
  .opacity {
    height: 72px;
    background: linear-gradient(to top, #fff, #fff, rgba(255, 255, 255, 0));
  }
  .conditions {
    margin-bottom: 60px;
  }
}

/* Iphone 6/7/8 */
@media screen and (max-width: 380px) and (max-height: 700px) {
  .main {
    padding-top: 138px;
    margin-bottom: 105px;
  }
  .close {
    top: 14px;
  }
  .opacity {
    height: 42px;
    background: linear-gradient(to top, #fff, rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0));
  }
  .conditions {
    margin-bottom: 30px;
  }
}

/* Iphone 5/SE */
@media only screen and (max-width: 330px) {
  .close {
    right: 0;
  }
  .unlock {
    font-size: 32px;
    line-height: 43px;
  }
  .plans__price_sale {
    font-size: 16px;
    margin-bottom: 0;
  }
  .plans__price_money {
    font-size: 12px;
  }
  .plans__action {
    max-width: 140px;
    font-size: 12px;
  }
  .options__item {
    font-size: 16px;
  }
  .main {
    margin-bottom: 40px;
  }
  .conditions {
    margin-bottom: 30px;
  }
  .opacity {
    background: linear-gradient(to top, #fff, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0));
  }
}
